:root {
    --main_color: #0A66C2;
    --text_color: #292929;
    --white_color: #ffffff;
    --hover_color: #3380cc;
    --clicked_color: #074481;
    --disabled_color: #a8a8a8;
    --side_navbar_width: 332px;
}

.verify1 {
    width: 100%;
    height: 100%;
    min-height: 100vh;
    display: flex;
    justify-content: center;
    align-items: center;
}

.sendOtp {
    width: 70%;
    display: flex;
    height: 100%;
    flex-direction: column;
    align-items: center;
}

.loading {
    display: flex;
    align-items: center;
    width: 100%;
    height: 40px;
    margin: 50px 0px;
}

.loading-text {
    font-size: 14px;
    font-weight: 400;
    line-height: 19px;
    margin-left: 9px;
}

.one-sendOtp,
.two-sendOtp,
.three-sendOtp,
.four-sendOtp {
    width: 40px;
    height: 40px;
    border-radius: 50%;
    font-size: 20px;
    font-weight: 400;
    line-height: 30px;
    background-color: var(--white_color);
    border: 1px solid var(--main_color);
    color: var(--main_color);
    display: flex;
    justify-content: center;
    align-items: center;
}

.one-sendOtp {
    background-color: var(--main_color);
    border: none;
    color: var(--white_color);
}

.line {
    flex-grow: 1;
    height: 0px;
    border: 0.5px solid #000000;
    margin: 0px 25px 0px 8px;
}

.verify {
    width: 90%;
    height: 733px;
    background-color: var(--white_color);
    text-align: center;
    display: flex;
    flex-direction: column;
    align-items: center;
    color: #1f1f1f;
}

.verify1 h1 {
    font-size: 56px;
    font-weight: 600;
    line-height: 84px;
    margin: 0;
}

.choose {
    font-size: 34px;
    font-weight: 400;
    line-height: 46px;
    margin: 0;
    margin: 24px 0px 16px 0px;
}

.proceed {
    font-size: 28px;
    font-weight: 400;
    line-height: 38px;
    margin-bottom: 68px;
}

.verify button {
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: space-between;
    width: 323px;
    height: 70px;
    background-color: var(--main_color);
    padding: 16px 32px;
    border: none;
    cursor: pointer;
    border-radius: 8px;
    color: var(--white_color);
    font-size: 28px;
    font-weight: 400;
    line-height: 38px;
}

.verify button:hover {
    background-color: var(--hover_color);
}

.verify button:active {
    background-color: var(--clicked_color);
}

.email {
    font-weight: 400;
    font-size: 34px;
    line-height: 46px;
    margin: 38px 0px 64px 0px;
}

@media screen and (max-width: 576px) {

    .sendOtp {
        width: 95%;
    }

    .loading {
        height: 20px;
    }

    .loading-text {
        display: none;
    }

    .one-sendOtp,
    .two-sendOtp,
    .three-sendOtp,
    .four-sendOtp {
        width: 20px;
        height: 20px;
        font-size: 10px;
        line-height: 14px;
        font-weight: 500;
    }

    .line {
        margin: 0px 5px;
    }

    .verify {
        width: 100%;
        height: auto;
    }

    .verify1 h1 {
        font-size: 40px;
        line-height: 55px;
        margin: 55px 0px 0px;
    }

    .choose {
        font-size: 28px;
        line-height: 38px;
        margin: 72px 0px 24px 0px;
        width: 90%;
    }

    .proceed {
        font-size: 20px;
        line-height: 27px;
        margin-bottom: 20px;
    }

    .verify svg {
        transform: scale(0.4);
        margin: -60px 0px -90px;
    }

    .verify button {
        margin-bottom: 89px;
    }

    .email {
        margin: 61px 0px 56px 0px;
        font-size: 28px;
        line-height: 38px;
    }
}