:root {
    --main_color: #0A66C2;
    --footer_color: #0855A2;
    --text_color: #292929;
    --white_color: #ffffff;
    --hover_color: #3380cc;
    --clicked_color: #074481;
    --disabled_color: #a8a8a8;
    --error_color: #FF4040;
}

.forgot-password {
    padding: 40px 74px;
    display: flex;
    flex-direction: column;
    height: 100vh;
}

.forget-home {
    background-color: rgba(0, 0, 0, 0);
    border: none;
}

.reset-section-container {
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
    height: 100%;
}

.reset-svg {
    display: none;
}

.reset-section {
    border: 2px solid var(--main_color);
    width: 90%;
    /* height: 60%; */
    max-width: 687px;
    /* max-height: 428px; */
    display: flex;
    flex-direction: column;
    align-items: center;
    padding: 32px;
}

.reset-close {
    width: 100%;
    display: flex;
    justify-content: flex-end;
}

.reset-close-btn {
    background-color: rgba(0, 0, 0, 0);
    border: none;
    cursor: pointer;
}

.reset-content {
    width: 100%;
    margin-top: 0px;
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
}

.reset-head {
    font-weight: 600;
    font-size: 32px;
    line-height: 44px;
    color: var(--text_color);
    margin: 0;
    margin-bottom: 24px;
    text-align: center;
}

.reset-text {
    margin: 0;
    margin-bottom: 32px;
    font-weight: 400;
    font-size: 18px;
    line-height: 25px;
    color: #0C0C0C;
    text-align: center;
}

.reset-form {
    width: 100%;
}

.reset-email {
    font-weight: 400;
    font-size: 16px;
    line-height: 22px;
    color: #242433;
}

.reset-input {
    width: 100%;
    height: 56px;
    padding: 16px;
    background-color: var(--white_color);
    border: 1px solid var(--main_color);
    border-radius: 8px;
    font-weight: 400;
    font-size: 16px;
    line-height: 22px;
    margin-top: 8px;
}

.reset-input:focus {
    outline-color: var(--main_color);
}

.reset-invalid-email {
    display: flex;
    align-items: center;
}

.reset-email-err {
    border: 1px solid var(--error_color);
}

.invalid-email-text {
    font-weight: 400;
    font-size: 14px;
    line-height: 19px;
    color: var(--error_color);
    margin-left: 8px;
}

.reset-submit {
    margin: 40px 0px 24px 0px;
    width: 100%;
    border-radius: 8px;
    height: 54px;
    background-color: var(--main_color);
    font-weight: 400;
    font-size: 16px;
    line-height: 22px;
    color: var(--white_color);
    border: none;
    cursor: pointer;
}

.reset-submit:hover {
    background-color: var(--hover_color);
}

.reset-submit:active {
    background-color: var(--clicked_color);
}

.reset-signup-text,
.reset-signup {
    font-weight: 500;
    font-size: 20px;
    line-height: 27px;
    color: var(--text_color);
    text-align: center;
}

.reset-signup {
    color: var(--main_color);
}

.reset-section2 {
    border: 2px solid var(--main_color);
    width: 90%;
    /* height: 60%; */
    max-width: 860px;
    /* max-height: 428px; */
    display: flex;
    flex-direction: column;
    align-items: center;
    padding: 32px;
}

.reset-head2 {
    font-weight: 600;
    font-size: 32px;
    line-height: 44px;
    color: var(--text_color);
    margin: 0;
    margin-bottom: 24px;
    text-align: center;
}

.reset-text2 {
    margin: 0;
    margin-bottom: 32px;
    font-weight: 400;
    font-size: 28px;
    line-height: 38px;
    color: #1f1f1f;
    text-align: center;
}

.reset-proceed {
    margin: 40px 0px 24px 0px;
    width: 100%;
    max-width: 323px;
    border-radius: 8px;
    height: 70px;
    background-color: var(--main_color);
    font-weight: 600;
    font-size: 28px;
    line-height: 38px;
    color: var(--white_color);
    border: none;
    cursor: pointer;
}

.reset-proceed:hover {
    background-color: var(--hover_color);
}

.reset-proceed:active {
    background-color: var(--clicked_color);
}

.reset-spam {
    font-weight: 500;
    font-size: 20px;
    line-height: 27px;
    color: var(--text_color);
    text-align: center;
}


.reset-pswd {
    /* max-width: 516px; */
    width: 100%;
    height: 48px;
    background: #FEFEFE;
    border: 1px solid #7E7E7E;
    border-radius: 8px;
    /* margin-bottom: 16px; */
    padding: 0px 8px;
    display: flex;
    align-items: center;
    font-size: 16px;
    font-weight: 400;
    line-height: 24px;
}

.reset-pswd input {
    padding: 8px;
    font-size: 16px;
    font-weight: 400;
    line-height: 24px;
    border: none;
    flex-grow: 1;
}

.reset-pswd input:focus {
    outline: none;
}

.reset-showpwd {
    background-color: rgba(0, 0, 0, 0);
    border: none;
    cursor: pointer;
}

.reset-pwd-container {
    width: 100%;
    display: flex;
    justify-content: center;
}

.reset-pwd-btn {
    margin: 40px 0px 24px 0px;
    width: 100%;
    max-width: 325px;
    border-radius: 8px;
    height: 57px;
    background-color: var(--main_color);
    font-weight: 400;
    font-size: 18px;
    line-height: 25px;
    color: var(--white_color);
    border: none;
    cursor: pointer;
}

.reset-pwd-btn:hover {
    background-color: var(--hover_color);
}

.reset-pwd-btn:active {
    background-color: var(--clicked_color);
}

@media screen and (max-width: 576px) {

    .forgot-password {
        padding: 10px;
    }

    .forgot-logo {
        display: none;
    }

    .reset-svg {
        display: block;
    }

    .reset-section {
        border: none;
        width: 100%;
        max-width: none;
        padding: 0;
        margin-top: 24px;
    }

    .reset-close {
        display: none;
    }

    .reset-head {
        font-weight: 400;
        font-size: 16px;
        line-height: 22px;
        margin-bottom: 25px;
    }

    .reset-text {
        font-size: 14px;
        line-height: 19px;
        margin-bottom: 15px;
    }

    .reset-input {
        height: 51px;
        font-weight: 400;
        font-size: 14px;
        line-height: 19px;
    }

    .reset-submit {
        height: 50px;
    }

    .reset-signup-text,
    .reset-signup {
        font-weight: 400;
        font-size: 14px;
        line-height: 19px;
    }

    .reset-section2 {
        border: none;
        width: 100%;
        max-width: none;
        padding: 0;
        margin-top: 82px;
    }

    .reset-head2 {
        font-weight: 600;
        font-size: 20px;
        line-height: 27px;
        margin-bottom: 8px;
    }

    .reset-text2 {
        font-size: 16px;
        line-height: 22px;
        margin-bottom: 24px;
    }

    .reset-sent {
        transform: scale(0.5);
    }

    .reset-proceed {
        height: 54px;
        max-width: 179px;
        font-weight: 400;
        font-size: 16px;
        line-height: 22px;
        margin: 24px 0px;
    }

    .reset-spam {
        font-weight: 400;
        font-size: 16px;
        line-height: 22px;
    }
}